.auth .container {
    display: flex;
    flex-direction: row;
    margin: 4rem auto;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.auth .form-wrapper {
    flex: 1;
    transition: transform 0.5s ease;
}

.auth .form-section {
    width: 100%;
    padding: 1rem;
    transition: opacity 0.5s ease;
    box-sizing: border-box;
}

.auth .left, .auth .right {
    padding: 1rem;
}

.auth h1 {
    padding: 1rem 0;
}

.auth .input-container {
    padding: 1rem 0;
}

.auth .img-container {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.auth img {
    max-width: 80%;
    height: auto;
    display: block;
}

@media screen and (max-width: 940px) {
    .auth .container {
        flex-direction: column;
        margin: 1rem 0;
    }

    .auth .form-wrapper, .auth .img-container {
        width: 100%;
    }
}

/* New CSS for switch form links */
.auth .switch-form {
    margin-top: 1rem;
    color: #007bff;
    cursor: pointer;
    text-align: center;
    font-weight: 500;
    font-size: 0.9rem;
    transition: color 0.3s ease, text-decoration 0.3s ease;
}

.auth .switch-form:hover {
    color: #0056b3;
    text-decoration: underline;
}

/* Additional styling for new input fields */
.auth .input-container input[type='date'] {
    width: 50%;
}
/* Add to Auth.css */

/* Transition for fade-out effect */
.auth .form-section.fade-out {
  opacity: 0;
  transition: opacity 0.5s ease;
}


